<template>
  <div class="detail">
    <!---->
    <div class="base-info">
      <span>{{ baseInfo.name }}</span> &nbsp;
      <span>{{ baseInfo.floor}}</span> &nbsp;
      <span>{{ baseInfo.usage == 1? '':'' }}</span> &nbsp;
      <span>{{ baseInfo.vacant == 1? '':'' }}</span>&nbsp;
      <span>{{ baseInfo.building }}</span>
    </div>
    <!---->
    <div class="booking-info">
      <p></p>
      <!---->
      <div class="booking-info-week">
        <el-select v-model="week" size="mini" placeholder="" @change="selectWeek">
          <el-option
            v-for="item in weeks"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>
      <div class="booking-info-table">
        <el-table
          :data="bookList"
          border
          size="mini"
          style="width: 100%"
          >
          <el-table-column
            prop="wid"
            label=""
            width="60">
          </el-table-column>
          <el-table-column
            prop="section1"
            label="9:00-10:20"
            width="86">
          </el-table-column>
          <el-table-column
            prop="section2"
            label="10:40-12:00"
            width="86">
          </el-table-column>
          <el-table-column
            prop="section3"
            label="14:00-15:20"
            width="86">
          </el-table-column>
          <el-table-column
            prop="section4"
            label="15:30-16:50"
            width="86">
          </el-table-column>
          <el-table-column
            prop="section5"
            label="17:00-18:20"
            width="86">
          </el-table-column>
          <el-table-column
            prop="section6"
            label="19:00-20:20"
            width="86">
          </el-table-column>
          <el-table-column
            prop="section7"
            label="20:40-22:00"
            width="86">
          </el-table-column>
          <el-table-column
            prop="status"
            label=""
           >
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
import { apis } from '../../../config/url.js'
import $http from '../../../module/axios.js'
import Cookie from '../../../module/cookie.js'

export default {
  name: 'detail',
  data () {
    return {
      baseInfo: {},     // 
      week: 0,
      weeks: [
        { value: 0, label: ''},
        { value: 1, label: '' },
        { value: 2, label: '' },
        { value: 3, label: '' },
        { value: 4, label: '' },
        { value: 5, label: '' }
      ],
      bookList: [],      // 
      labName: '',       // 

    }
  },
  created () {
    this.getIndex()
    this.getBookList()
  },
  methods: {
    /**
     * 
     */
    getIndex () {
      var query = this.$route.query
      var index = query.index
      var _that = this

      if (index != null) {
        $http.post(apis.getLaboratoryById, {
          id: index
        })
        .then(res => {
          _that.baseInfo = res.data.laboratory
        })
      }
    },

    /**
     * 
     */
    getBookList () {
      var query = this.$route.query
      var index = query.index
      var _that = this

      $http.post(apis.getLabBookingInfo, {
        id: index
      })
      .then(res => {
        _that.bookList = res.data.list
      })
    },

    /**
     * ,
     */
    selectWeek (val) {
      var index = this.$route.query.index
      var _that = this

      if (val == 0) {
        this.getBookList()
      } else {
        $http.post(apis.getLabBookingInfoByWeek, {
          lId: index,
          wId: val
        })
        .then(res => {
          _that.bookList = res.data.list
        })
      }
    }
  },

  // 
  watch: {
    // 
    "$route": function(newValue,oldValue){
        this.getIndex()
        this.getBookList()

    }
  }
}

</script>

<style scoped>
 .detail {
   padding: 20px 30px;
 }
  .el-icon-warning {
    color: #106f00;
  }

  /**/
  .base-info > span {
    color: #67c23a;
  }

  /**/
  .booking-info {
    margin: 20px 0;
  }
  .booking-info-week {
    margin-bottom: 20px;
  }
</style>



