<template>
  <div class="announcementDetail" :style="announcementDetailNewStyle"> <!---->
    <div class="announcementDetail__head">
      
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/announcement' }"></el-breadcrumb-item>
        <el-breadcrumb-item>{{ operation }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="announcementDetail__body">
      <div class="announcementDetail__body-explain">
        {{ operation }}
      </div>
      <div class="announcementDetail__body-header">

        <el-form :model="announcement" :rules="rules" ref="announcement" label-width="80px" status-icon>
          <el-form-item label="" prop="title">
            <el-input v-model="announcement.title" size="small" placeholder=""></el-input>
          </el-form-item>
          <!--
          <el-form-item label="">
            <el-input v-model="announcement.subtitle" size="small" placeholder=","></el-input>
          </el-form-item>
          -->
          <el-form-item label="">
            {{ announcement.author }}
          </el-form-item>
          <el-form-item label="" prop="time">
            <el-date-picker
              v-model="announcement.date"
              type="date"
              placeholder=""
              value-format="yyyy-MM-dd">
            </el-date-picker>
          </el-form-item>
        </el-form>

      </div>
      <div class="announcementDetail__body-editor">
        <div class="editor_left">
          <!-- wangEditor -->
          <div id="editorElem" style="text-align:left">

          </div>
        </div>
        <!--  -->
        <div class="editor_right">
          <!--
          <div class="editor_right-add" v-if="operationType == 1">
            <div><el-button type="primary" plain @click="previewAnnouncement"></el-button></div>
            <div><el-button type="primary" plain @click="publishAnnouncement(1)"></el-button></div>
            <div><el-button type="primary" plain @click="publishAnnouncement(2)"></el-button></div>
            <div><el-button type="primary" plain @click="clearAnnouncement"></el-button></div>
          </div>
          <div class="editor_right-editor" v-else-if="operationType == 2">
            <div><el-button type="primary" plain @click="previewAnnouncement"></el-button></div>
            <div><el-button type="primary" plain @click="saveUpdate"></el-button></div>
            <div><el-button type="primary" plain @click="clearAnnouncement"></el-button></div>
          </div>
          <div class="editor_right-spare" v-else></div>
        </div>
        -->
        <div class="editor_right-same1">
            <div>
              <el-tooltip class="item" effect="dark" content="" placement="left">
                <router-link :to="{path:'/announcement/preview'}">
                  <el-button type="primary" plain @click="previewAnnouncement"></el-button>
                </router-link>
              </el-tooltip>
            </div>
          </div>
          <div class="editor_right-add" v-if="operationType == 1">
            <div><el-button type="primary" plain @click="publishAnnouncement(1)"></el-button></div>
            <div><el-button type="primary" plain @click="publishAnnouncements(2)"></el-button></div>
          </div>
          <div class="editor_right-editor" v-else-if="operationType == 2">
            <div><el-button type="primary" plain @click="saveUpdate"></el-button></div>
          </div>
          <div class="editor_right-spare" v-else></div>
          <div class="editor_right-same2">
            <div><el-button type="primary" plain @click="clearAnnouncement"></el-button></div>
            <div>
              <el-popover ref="attention" placement="left" title="" width="200" trigger="hover"
              content="">
              </el-popover>
              <el-button type="warning" plain icon="el-icon-warning" v-popover:attention></el-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import E from 'wangeditor'
import { apis } from '../../../config/url.js'
import $http from '../../../module/axios.js'
import FormaterTime from '../../../module/time.js'
import Cookies from '../../../module/cookie.js'
import { mapGetters, mapMutations, mapActions } from 'vuex'

export default {
  name: 'announcementDetail',
  data () {
    return {
      datetime :{
        date: '',
        time: ''
      },
      operationType: 0,     // 1-/2-
      operationIndex: -1,   // 
      editorObj: null,      // wangEditor
      announcement: {
        id: -1,
        title: '',          // 
        author: '',         // 
        date: '',           // 
        contents: '',        // 
        state: -1,          // 
        visits: 0          // 
      },
      rules: {              // 
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 20, message: ' 1  20 ', trigger: 'blur' }
        ],
        date: [
          { type: 'date', required: true, message: '', trigger: 'change' }
        ]
      },
      operation: '',          // 
      announcementDetailNewStyle: {}  // announcement
    }
  },

  created () {
    // 
    this.changePageStyle(this.getIsCollapse)
    var _that = this

    var path = this.$route.path
    if (path.indexOf('/announcement/add') > -1) {
      _that.operationType = 1
      _that.operation = ""

    } else if (path.indexOf('/announcement/edit') > -1) {
      _that.operationType = 2
      _that.operationIndex = this.$route.query.id
       _that.operation = ""
    }

    // 
    if (_that.operationType == 2) {

      /**
      * ID
      */
      $http.post(apis.getAnnouncementById, {
        id: _that.operationIndex
      })
      .then(res => {
        _that.announcement = res.data.announcement
        _that.announcement.date = new Date(res.data.announcement.date).format('yyyy-MM-dd hh:mm:ss', false)
        console.log(_that.announcement.date)
        // 
        this.editorObj.$txt.html(this.announcement.contents)
      })
    }

  },

  mounted () {
    // 
    FormaterTime()

    // cookie
    var currentUser = Cookies.get('CURRENTUSER')
    this.announcement.author = currentUser || ''

    // 
    var editor = new E('editorElem')
    // editor.onchange = () => {
    //   this.announcement.contents = editor.$txt.html()
    // }
    editor.create()
    this.editorObj = editor

  },
  computed: {
    ...mapGetters ([
      'getIsCollapse',            // 
      'getOperationAnnouncementIndex'     // 1-/2-
    ])
  },
  methods: {
    /**
     * vuexmutation
     */
    ...mapMutations([
      'setIsCollapse',
      'setAnnouncementPreviewDatas'
    ]),

    /**
     * 
     */
    changePageStyle (val) {
      if (val == true) {
        this.announcementDetailNewStyle = {
          left: '64px',
          width: 'calc(100% - 64px)',
          transition: 'width 0.35s, left 0.35s',
          MozTransition: 'width 0.35s, left 0.35s',
          WebkitTransition: 'width 0.35s, left 0.35s',
          OTransition: 'width 0.35s, left 0.35s'
        }
      } else {
        this.announcementDetailNewStyle = {
          left: '200px',
          width: 'calc(100% - 200px)',
          transition: 'width 0.6s, left 0.6s',
          MozTransition: 'width 0.6s, left 0.6s',
          WebkitTransition: 'width 0.6s, left 0.6s',
          OTransition: 'width 0.6s, left 0.6s'
        }
      }
    },

    /**
     * 
     */
    previewAnnouncement () {
      var announcement = {
        title: this.announcement.title,
        author: this.announcement.author,
        date: this.announcement.date,
        visits:  this.announcement.visits,
        contents: this.editorObj.$txt.html()
      }

      // vewxstore
      this.setAnnouncementPreviewDatas(announcement)
    },

    /**
     * /
     */
    publishAnnouncement (type) {
      var warn = ''
      var words = ''
      if (type == 1) {
        warn = ''
        words = ''
      } else {
        warn = ''
        words = ''
      }

      this.$confirm(''+warn+'?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {

        // 
        if (this.announcement.date == '' || this.announcement.date == null) {
          this.announcement.date = new Date().format('yyyy-MM-dd hh:mm:ss', false)
        }

        // 
        if (this.announcement.title == '' || this.announcement.title == null) {
          this.$message({
            type: 'error',
            message: ':!',
            center: true
          });
          return false
        }

        // 
        var contents = this.editorObj.$txt.html() // 
        if (contents == '' || contents == '<p><br></p>') {
          this.$message({
            type: 'error',
            message: ':!',
            center: true
          })
          return false
        } else {
          this.announcement.contents = contents
        }

        // 
        var _that = this
        this.announcement.state = type   // 1-2-
        $http.post(apis.addAnnouncement, this.announcement)
        .then(res => {
          if (res.code== 1) {  // 1
            _that.$message({
              type: 'success',
              message: words + '!',
              center: true
            })

            // 
            _that.$router.push('/announcement')
          } else {
          _that.$message({
            type: 'error',
            message: words + '!',
            center: true
          })
          }
        })

      }).catch(() => {
        this.$message({
          type: 'info',
          message: '' + words,
          center: true
        })
      })
    },

    /**
     * 
     */
    clearAnnouncement () {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // , 
        this.editorObj.$txt.html('<p><br></p>');

        this.announcement.title = ''
        this.announcement.subtitle = ''
        this.announcement.date = ''

        this.$message({
          type: 'success',
          message: '!',
          center: true
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '',
          center: true
        })
      })
    },

    /**
     * 
     */
    saveUpdate () {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {

        // 
        if (this.announcement.date == '' || this.announcement.date == null) {
          this.announcement.date = new Date().format('yyyy-MM-dd hh:mm:ss', false)
        }

        // 
        if (this.announcement.title == '' || this.announcement.title == null) {
          this.$message({
            type: 'error',
            message: ':!',
            center: true
          });
          return false
        }

        // 
        var contents = this.editorObj.$txt.html() // 
        if (contents == '' || contents == '<p><br></p>') {
          this.$message({
            type: 'error',
            message: ':!',
            center: true
          })
          return false
        } else {
          this.announcement.contents = contents
        }

        // 
        var _that = this
        $http.post(apis.updateAnnouncementById, _that.announcement)
        .then(res => {
          if (res.code== 1) {  // 1
            _that.$message({
            type: 'success',
            message: '!',
            center: true
          })
          } else {
          _that.$message({
            type: 'error',
            message: '!',
            center: true
          })
          }
        })

      }).catch((err) => {
        this.$message({
          type: 'info',
          message: '',
          center: true
        })
      })
    },

    /**
     * 
     */
    validated () {

      // 
      var state = true

      // 
      if (this.announcement.date == '' || this.announcement.date == null) {
        this.announcement.date = new Date().format('yyyy-MM-dd hh:mm:ss', false)
        state = true
      }

      // 
      if (this.announcement.title == '' || this.announcement.title == null) {
        this.$message({
          type: 'error',
          message: ':!',
          center: true
        });
        state = false;
      } else {
        state = true
      }

        // 
      if (this.announcement.editorContent == '' || this.announcement.editorContent == '<p><br></p>') {
          this.$message({
          type: 'error',
          message: ':!',
          center: true
        });
        state = false;
      } else {
        state = true;
      }
    }

  },

  watch: {
    getIsCollapse: {
      handler (val, oldVal) {
        this.changePageStyle(val)
      }
    }
  }
}
</script>

<style scoped>
  .announcementDetail {
    position: relative;
    top: 62px;
    left: 200px;
    width: calc(100% - 200px);
    height: 800px;
    font-size: 14px;
    overflow-y: scroll;
  }

  .announcementDetail__head {
    position: relative;
    width: 100%;
    height: 30px;
    color: #999999;
    padding: 3px 0 3px 20px;
  }
  .announcementDetail__body {
    position: relative;
    width: 100%;
    height: 1000px;
  }
  .announcementDetail__body-explain,
  .announcementDetail__body-header,
  .announcementDetail__body-editor {
    position: relative;
    margin: 10px 20px;
  }
  .announcementDetail__body-explain {
    color: #409eff;
    font-weight: bold;
    font-size: 16px;
  }
  .announcementDetail__body-header {
    width: 56%;
    text-align: left;
  }
  .announcementDetail__body-editor {
    width: 100%;
    height: auto;
  }
  .announcementDetail__body-editor::after {
    display: block;
    content: '';
    clear: both;
  }
  #editorElem {
    height: 620px;
    min-height: 500px;
  }
  .editor_left,
  .editor_right {
    float: left;
  }
  .editor_left {
    width: 84%;
    margin-right: 10px;
  }
  .editor_right-same1 > div,
  .editor_right-same2 > div,
  .editor_right-add > div,
  .editor_right-editor > div {
    margin-bottom: 10px;
  }
</style>
<style>
  .el-form-item__label {
    text-align: center;
  }
  .el-input-group__append {
    background-color: #ffffff;
    color: #f5131c;
    border: none;
  }
  .el-breadcrumb {
    display: inline-block;
    line-height: 1;
  }
  .el-breadcrumb__inner,
  .el-breadcrumb__inner a {
    font-weight: normal;
    color: #999999;
  }
  .el-breadcrumb__item:last-child .el-breadcrumb__inner,
  .el-breadcrumb__item:last-child .el-breadcrumb__inner:hover {
    color: #999999;
  }
</style>



