<template>
  <div class="resource" :style="resourceNewStyle">
    <div class="resource__head">
      <a href="javascript:;"></a> >>
      <a href="javascript:;" @click="refleshList"></a>
    </div>
    <div class="resource__body">
      <div class="resource__body-options">
        <el-button type="success" size="mini" plain icon="el-icon-delete" @click="deleteMulti">
          
        </el-button>
        <el-button type="success" size="mini" icon="el-icon-upload" plain @click="changeUpload">
          
        </el-button>
        &nbsp;&nbsp;
        <i class="el-icon-refresh" @click="refleshList"></i>
        &nbsp;&nbsp;
        <div class="resource__body-options-search">
          <div @keyup.enter="searchResource">
            <el-input placeholder="" v-model="searchInfo" class="input-with-select"
            suffix-icon="el-icon-edit" size="mini" >
              <span slot="prepend"></span>
              <el-button type="success" slot="append" icon="el-icon-search" @click="searchResource"></el-button>
            </el-input>
          </div>
        </div>
      </div>
      <div class="resource__body-content">
        <div class="content__table">
          <el-table ref="multipleTable" :data="getFileListPaging" tooltip-effect="dark"
          style="width: 100%" @selection-change="handleSelectionChange"
          :default-sort = "{prop: 'id', order: 'ascending'}" >

            <el-table-column type="selection" prop="id" width="55">
            </el-table-column>
            <el-table-column label="" width="220" prop="filename" >
            </el-table-column>
            <el-table-column prop="url" label="" show-overflow-tooltip>
            </el-table-column>
            <el-table-column prop="type" label="" width="200">
            </el-table-column>
            <el-table-column prop="size" label="" width="100">
              <template slot-scope="scope">
                {{ (scope.row.size / 1024).toFixed(2) + 'KB'}}
              </template>
            </el-table-column>
            <el-table-column prop="download" label="" width="50">
            </el-table-column>
            <el-table-column  label="" width="80">
              <template slot-scope="scope">
                <i class="el-icon-delete" @click="deleteSinple(scope.row.id)"></i>
                &nbsp;&nbsp;
                <a :href="scope.row.url" target="_blank"><i class="el-icon-download"></i></a>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="content__pagination">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[5, 6]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="resourceNumber">
          </el-pagination>
        </div>
      </div>
    </div>
    <!---->
    <div v-show="showUpload">
      <upload v-on:listenToChildEvent="changeUploadStatus"></upload>
    </div>
  </div>
</template>

<script>
import Upload from './component/upload.vue'
import { apis } from '../../config/url.js'
import $http from '../../module/axios.js'
import { mapGetters, mapMutations } from 'vuex'

export default {
  name: 'resource',
  components: {
    Upload
  },
  data () {
    return {
      resourceNewStyle: {},       // resource
      currentPage: 1,             // 
      pageSize: 6,                // 8
      multipleSelection: [],      // checkbox
      searchInfo: '',             // 
      searchType: '',             // 
      selectIndexs: [],           // 
      editIndex: -1,              // 
      resourceList: [],           // 
      resourceNumber: 0,          // ,
      showUpload: false,          // 
    }
  },
  created() {
    let sh = document.documentElement.clientHeight
    this.resourceNewStyle = {
      height: sh + 'px'
    }

    this.changePageStyle(this.getIsCollapse) // 

    // 
    this.updateList()
    this.countResourceNumber()
  },
  computed: {
    ...mapGetters ([
      'getIsCollapse',         // 
      'getFileListPaging'
    ])
  },
  methods: {
    /**
     * vuexmutation
     */
    ...mapMutations([
      'setIsCollapse',          // 
      'setFileList'
    ]),
    /**
     * 
     */
    changePageStyle (val) {
      let sh = document.documentElement.scrollHeight
      if (val == true) {
        this.resourceNewStyle = {
          left: '64px',
          width: 'calc(100% - 64px)',
          height: sh  + 'px',
          transition: 'width 0.35s, left 0.35s',
          MozTransition: 'width 0.35s, left 0.35s',
          WebkitTransition: 'width 0.35s, left 0.35s',
          OTransition: 'width 0.35s, left 0.35s'
        }
      } else {
        this.resourceNewStyle = {
          left: '200px',
          width: 'calc(100% - 200px)',
          height: sh + 'px',
          transition: 'width 0.6s, left 0.6s',
          MozTransition: 'width 0.6s, left 0.6s',
          WebkitTransition: 'width 0.6s, left 0.6s',
          OTransition: 'width 0.6s, left 0.6s'
        }
      }
    },

    /**
     * 
     */
    changeUpload () {
      if (this.showUpload == false) {
        this.showUpload = true
      } else {
        this.showUpload = false
      }
    },
    /**
     * 
     */
    changeUploadStatus (data) {
      this.showUpload = data
      // 
      this.refleshList()
    },
    /**
     * 
     */
    updateList (current = this.currentPage, size = this.pageSize) {
      var _that = this
      /**
      * 
      */
      $http.post(apis.getFilesByPaging, {
        currentPage: current,  // 
        pageSize: size         // 
      })
      .then(res => {
        var datas = res.data.list
        // mutationstore
        _that.setFileList(datas)
      })
    },
    /**
     * 
     */
    countResourceNumber () {
      var _that = this
      /**
      * 
      */
      $http.post(apis.getFilesNumber,{})
      .then(res => {
        // 
        _that.resourceNumber = res.number
      })
    },

    /**
     * 
     */
    refleshList () {
      this.updateList()
      this.countResourceNumber()
    },
    /**
     * :
     */
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    /**
     * :
     */
    handleSelectionChange(selection) {
      this.multipleSelection = selection;
    },
    /**
     * :
     */
    deleteSinple(id) {
      if (id != null) {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          // 
          $http.post(apis.removeFileById, {
            id: id,
          })
          .then(res => {
            if (res.code== 1) {
              this.$message({
                type: 'success',
                message: '!',
                center: true
              })

              // 
              this.updateList()
              this.countResourceNumber()
            } else {
              this.$message({
                type: 'error',
                message: '!',
                center: true
              })
            }
          })
        })
        .catch((err) => {
        this.$message({
          type: 'info',
          message: '',
          center: true
        })
      })
      } else {
        this.$message({
          type: 'error',
          message: '!',
          center: true
        })
      }
    },
    /**
     * :
     */
    deleteMulti() {
      // 
      var arr = this.multipleSelection

      if (arr.length === 0) {
        this.$message({
          type: 'warning',
          message: '!',
          center: true
        })
        return;
      } else {
        var list = []
        // ID
        for (var i =0; i < arr.length; i++) {
          list.push(arr[i].id)
        }

        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          // 
          $http.post(apis.removeMultiFiles, {
            idList: list,
          })
          .then(res => {
            if (res.code== 1) {
              this.$message({
                type: 'success',
                message: '!',
                center: true
              })

              // 
              this.updateList()
              this.countResourceNumber()
            } else {
              this.$message({
                type: 'error',
                message: '!',
                center: true
              })
            }
          })
        })
        .catch((err) => {
          this.$message({
            type: 'info',
            message: '',
            center: true
          })
        })
      }
    },
    /**
     * 
     */
    downloadFile(url) {
      $http.download(url, {})
      .then(res => {
      })
    },
    /**
     * :
     */
    searchResource() {
      var value = this.searchInfo // 

      if (value == null || value == '') {
        this.$message({
          type: 'warning',
          message: '',
          center: true
        })
        return;
      } else {
        var _that = this
        // 
        $http.post(apis.getFileByName, {
          filename: value
        })
        .then(res => {
          if (res.code== 1) {
            var datas = res.data.list
            // mutationstore
            _that.setFileList(datas)
            // 
            _that.resourceNumber = datas.length
          }
        })
      }
    },
    /**
     * :
     */
    addResource() {
      this.setOperationResourceIndex({
        operationType: 1,        // 1-/2-
        operationResourceIndex: -1   // ID-1
      })
    },
    /**
     * :
     */
    editResource(id) {
      this.setOperationResourceIndex({
        operationType: 2,    // 1-/2-
        operationResourceIndex: id   // ID,
      })
    },
    /**
     * :
     */
    handleSizeChange(size) {
      if (size != null && size > 0) {
        this.pageSize = size
        // 
        this.updateList(1, size)
      }
    },
    /**
     * :
     */
    handleCurrentChange(currentPage) {
      if (currentPage != null && currentPage > 0) {
        // 
        this.updateList(currentPage)
      }
    }
  },

  watch: {
    getIsCollapse: {
      handler (val, oldVal) {
        this.changePageStyle(val)
      }
    }
  }
}
</script>

<style scoped>
  .resource {
    position: relative;
    top: 62px;
    left: 200px;
    width: calc(100% - 200px);
    min-width: 560px;
    overflow: hidden;
    overflow-y: scroll;
    font-size: 14px;
  }
  .resource__head {
    position: relative;
    width: 100%;
    height: 30px;
    color: #999999;
    padding: 3px 0 3px 10px;
  }
  .resource__head a {
    color: #409eff;
    text-decoration: none;
  }
  .resource__body {
    position: relative;
    width: 100%;
    height: auto;
  }
  .resource__body-options {
    position: relative;
    margin: 8px 10px;
  }
  /*--------*/
  .resource__body-options-search {
    position: absolute;
    right: 0;
    display: inline-block;
    margin-bottom: 15px;
  }
  .resource__body-options-search > div {
    margin: 0;
    float: left;
  }
  .resource__body-options-search .el-button--mini {
    border-radius: 0 3px 3px 0;
  }
  .resource__body-options-search::after {
    display: block;
    content: '';
    clear: both;
  }
  .search__input {
    width: 250px;
    border-radius: 3px 0 0 3px;
  }
  .search__input > input {
    border-radius: 3px 0 0 3px;
  }
  /*--------*/

  .resource__body-content {
    width: 100%;
  }

  /*--------*/
  .content__table {
    margin: 5px 10px;
    border-top: 1px solid #ebeef5;
  }
  .el-icon-delete,
  .el-icon-download {
    color: #67c23a;
    font-size: 18px;
    cursor: pointer;
  }
  .el-icon-delete:hover,
  .el-icon-download:hover {
    color: orange;
  }
  /*--------*/

  /**/
  .content__pagination {
    margin-top: 20px;
    text-align: center;
  }

</style>
<style>
  .el-icon-refresh {
    color: #67c23a;
    font-size: 18px;
    cursor: pointer;
  }
  .el-select .el-input {
    width: 100px;
  }
  .input-with-select .el-input-group__prepend {
    background-color: #ffffff;
  }
  .el-input-group__append,
  .el-input-group__prepend {
    padding: 0 15px;
  }
  .el-input-group__append button.el-button {
    color: #409eff;
  }
  .el-input-group__append button.el-button:hover {
    color: 	#ffa500;
  }
  .resource__body-options-search .el-select
  .el-input.is-focus .el-input__inner {
    width: 160px;
  }
  .el-input-group>.el-input__inner {
    width: 210px;
  }
</style>



