<template>
  <div class="newsDetail" :style="newsDetailNewStyle"> <!---->
    <div class="newsDetail__head">
      
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/news' }"></el-breadcrumb-item>
        <el-breadcrumb-item >{{ operation }}</el-breadcrumb-item>
      </el-breadcrumb>

    </div>
    <div class="newsDetail__body">
      <div class="newsDetail__body-explain">
        {{ operation }}
      </div>
      <div class="newsDetail__body-header">

        <el-form :model="news" :rules="rules" ref="news" label-width="80px" status-icon>
          <el-form-item label="" prop="title">
            <el-input v-model="news.title" size="small" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="news.subtitle" size="small" placeholder=","></el-input>
          </el-form-item>
          <el-form-item label="">
            {{ news.author }}
          </el-form-item>
          <el-form-item label="" prop="time">
            <el-date-picker
              v-model="news.date"
              type="datetime"
              placeholder=""
              value-format="yyyy-MM-dd hh:mm:ss">
            </el-date-picker>
          </el-form-item>
          <el-form-item  class="newsDetail__body-imageNews" label="">
            <div>
              <el-tooltip :content="carousel == 1?'':''" placement="top" >
                <el-switch v-model="carousel" active-color="#13ce66" active-value="1"
                inactive-value="0"></el-switch>
              </el-tooltip>
            </div>
            <div class="carousel__url">
              <el-input class="carousel__url-input" size="small" placeholder=""
              v-show="carousel == 1" v-model="imageUrl"></el-input>
            </div>
            <div class="imageNews__carousel">
              <el-input class="carousel__url-input" size="small" placeholder=""
              v-show="carousel == 1" v-model="imageDescription"></el-input>
            </div>
          </el-form-item>
        </el-form>

      </div>
      <div class="newsDetail__body-editor">
        <div class="editor_left">
          <!-- wangEditor -->
          <div id="editorElem" style="text-align:left">
          </div>
        </div>
        <!--  -->
        <div class="editor_right">
          <div class="editor_right-same1">
            <div>
              <el-tooltip class="item" effect="dark" content="" placement="left">
                <router-link :to="{path:'/news/preview'}">
                  <el-button type="primary" plain @click="previewNews"></el-button>
                </router-link>
              </el-tooltip>
            </div>
          </div>
          <div class="editor_right-add" v-if="operationType == 1">
            <div><el-button type="primary" plain @click="publishNews(1)"></el-button></div>
            <div><el-button type="primary" plain @click="publishNews(2)"></el-button></div>
          </div>
          <div class="editor_right-editor" v-else-if="operationType == 2">
            <div><el-button type="primary" plain @click="saveUpdate"></el-button></div>
          </div>
          <div class="editor_right-spare" v-else></div>
          <div class="editor_right-same2">
            <div><el-button type="primary" plain @click="clearNews"></el-button></div>
            <div>
              <el-popover ref="attention" placement="left" title="" width="200" trigger="hover"
              content="">
              </el-popover>
              <el-button type="warning" plain icon="el-icon-warning" v-popover:attention></el-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import E from 'wangeditor'
import { apis } from '../../../config/url.js'
import $http from '../../../module/axios.js'
import FormaterTime from '../../../module/time.js'
import Cookies from '../../../module/cookie.js'
import { mapGetters, mapMutations, mapActions } from 'vuex'

export default {
  name: 'newsDetail',
  data () {
    return {
      carousel: '',           // 
      imageUrl: '',           // 
      imageDescription: '',   // 
      datetime :{
        date: '',
        time: ''
      },
      operationType: 0,     // 1-/2-
      operationIndex: -1,   // 
      editorObj: null,      // wangEditor
      news: {
        id: -1,
        title: '',          // 
        subtitle: '',       // 
        author: '',         // 
        date: '',           // 
        contents: '',        // 
        state: -1,          // 
        visits: 0          // 
      },
      rules: {              // 
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 50, message: ' 1  50 ', trigger: 'blur' }
        ],
        date: [
          { type: 'date', required: true, message: '', trigger: 'change' }
        ]
      },
      operation: '',          // 
      newsDetailNewStyle: {}  // news
    }
  },

  created () {
    // 
    this.changePageStyle(this.getIsCollapse)
    var _that = this

    var path = this.$route.path
    if (path.indexOf('/news/add') > -1) {
      _that.operationType = 1
      _that.operation = ''

    } else if (path.indexOf('/news/edit') > -1) {
      _that.operationType = 2
      _that.operationIndex = this.$route.query.id
       _that.operation = ''
    }

    // 
    if (_that.operationType == 2) {
      this.getNewsDetail(this.$route.query.id)
    }

  },

  mounted () {
    // 
    FormaterTime()

    // cookie
    var currentUser = Cookies.get('CURRENTUSER')
    this.news.author = currentUser || ''

    // 
    var editor = new E('editorElem')
    // editor.onchange = () => {
    //   this.news.contents = editor.$txt.html()
    // }
    editor.create()
    this.editorObj = editor

  },
  computed: {
    ...mapGetters ([
      'getIsCollapse',            // 
      'getOperationNewsIndex'     // 1-/2-
    ])
  },
  methods: {
    /**
     * vuexmutation
     */
    ...mapMutations([
      'setIsCollapse',
      'setNewsPreviewDatas'
    ]),

    /**
     * 
     */
    changePageStyle (val) {
      if (val == true) {
        this.newsDetailNewStyle = {
          left: '64px',
          width: 'calc(100% - 64px)',
          transition: 'width 0.35s, left 0.35s',
          MozTransition: 'width 0.35s, left 0.35s',
          WebkitTransition: 'width 0.35s, left 0.35s',
          OTransition: 'width 0.35s, left 0.35s'
        }
      } else {
        this.newsDetailNewStyle = {
          left: '200px',
          width: 'calc(100% - 200px)',
          transition: 'width 0.6s, left 0.6s',
          MozTransition: 'width 0.6s, left 0.6s',
          WebkitTransition: 'width 0.6s, left 0.6s',
          OTransition: 'width 0.6s, left 0.6s'
        }
      }
    },

    /**
     * 
     */
    getNewsDetail (id) {
      var _that = this
      //ID
      $http.post(apis.getNewsById, {
        id: parseInt(id)
      })
      .then(res => {
        _that.news = res.data.news
        _that.news.date = new Date(res.data.news.date).format('yyyy-MM-dd hh:mm:ss', false)
        // 
        _that.editorObj.$txt.html(_that.news.contents)
      })

      // ID
      $http.post(apis.getImageNewsById, {
        nId: parseInt(id)
      })
      .then(res => {
        var datas = res.data
        if (datas != undefined) {
          _that.carousel = datas.imageNews.isShow.toString()
          _that.imageUrl = datas.imageNews.url
          _that.imageDescription = datas.imageNews.description
        }
      })
    },

    /**
     * 
     */
    previewNews () {
      var news = {
        title: this.news.title,
        subtitle: this.news.subtitle,
        author: this.news.author,
        date: this.news.date,
        visits:  0,     //this.news.visits
        contents: this.editorObj.$txt.html()
      }

      // vewxstore
      this.setNewsPreviewDatas(news)
    },

    /**
     * /
     */
    publishNews (type) {
      var warn = ''
      var words = ''
      if (type == 1) {
        warn = ''
        words = ''
      } else {
        warn = ''
        words = ''
      }

      this.$confirm(''+warn+'?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {

        // 
        if (this.news.date == '' || this.news.date == null) {
          this.news.date = new Date().format('yyyy-MM-dd hh:mm:ss', false)
        }

        // 
        if (this.news.title == '' || this.news.title == null) {
          this.$message({
            type: 'error',
            message: ':!',
            center: true
          });
          return false
        }

        // 
        var contents = this.editorObj.$txt.html() // 
        if (contents == '' || contents == '<p><br></p>') {
          this.$message({
            type: 'error',
            message: ':!',
            center: true
          })
          return false
        } else {
          this.news.contents = contents
        }

        // 
        var _that = this
        this.news.state = type   // 1-2-
        $http.post(apis.addNews, this.news)
        .then(res => {
          if (res.code== 1) {  // 1
            _that.$message({
              type: 'success',
              message: words + '!',
              center: true
            })
            // 
            _that.$router.push('/news')
          } else {
          _that.$message({
            type: 'error',
            message: words + '!',
            center: true
          })
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '' + words,
          center: true
        })
      })

      // 
      if (carousel == 1) {
        this.addImageNews(this.imageUrl,this.imageDescription)
      }
    },

    /**
     * 
     */
    addImageNews (url,description) {
      if (url != null&& description != null) {
        $http.post(apis.addImageNews, {
          url: url,
          description: description
        })
        .then(res => {})
      } else {
        this.$message({
          type: 'error',
          message: '!',
          center: true
        })
      }
    },

    /**
     * 
     */
    clearNews () {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // , 
        this.editorObj.$txt.html('<p><br></p>');

        this.news.title = ''
        this.news.subtitle = ''
        this.news.date = ''

        this.$message({
          type: 'success',
          message: '!',
          center: true
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '',
          center: true
        })
      })
    },

    /**
     * 
     */
    saveUpdate () {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {

        // 
        if (this.news.date == '' || this.news.date == null) {
          this.news.date = new Date().format('yyyy-MM-dd hh:mm:ss', false)
        }

        // 
        if (this.news.title == '' || this.news.title == null) {
          this.$message({
            type: 'error',
            message: ':!',
            center: true
          });
          return false
        }

        // 
        var contents = this.editorObj.$txt.html() // 
        if (contents == '' || contents == '<p><br></p>') {
          this.$message({
            type: 'error',
            message: ':!',
            center: true
          })
          return false
        } else {
          this.news.contents = contents
        }

        // 
        var _that = this
        $http.post(apis.updateNewsById, {
          id: _that.news.id,
          title: _that.news.title,             // 
          subtitle: _that.news.subtitle,       // 
          author: _that.news.author,           // 
          date: _that.news.date,               // 
          contents: _that.news.contents,       // 
          state: _that.news.state,             // 
          visits: _that.news.visits,
          isShow: parseInt(_that.carousel),
          url: _that.imageUrl,
          description: _that.imageDescription
        })
        .then(res => {
          if (res.code== 1) {  // 1
            _that.$message({
            type: 'success',
            message: '!',
            center: true
          })
          } else {
          _that.$message({
            type: 'error',
            message: '!',
            center: true
          })
          }
        })
      }).catch((err) => {
        this.$message({
          type: 'info',
          message: '',
          center: true
        })
      })
    },

    /**
     * 
     */
    validated () {

      // 
      var state = true

      // 
      if (this.news.date == '' || this.news.date == null) {
        this.news.date = new Date().format('yyyy-MM-dd hh:mm:ss', false)
        state = true
      }

      // 
      if (this.news.title == '' || this.news.title == null) {
        this.$message({
          type: 'error',
          message: ':!',
          center: true
        });
        state = false;
      } else {
        state = true
      }

        // 
      if (this.news.editorContent == '' || this.news.editorContent == '<p><br></p>') {
          this.$message({
          type: 'error',
          message: ':!',
          center: true
        });
        state = false;
      } else {
        state = true;
      }
    }

  },

  watch: {
    getIsCollapse: {
      handler (val, oldVal) {
        this.changePageStyle(val)
      }
    }
  }
}
</script>

<style scoped>
  .newsDetail {
    position: relative;
    top: 62px;
    left: 200px;
    width: calc(100% - 200px);
    height: 900px;
    font-size: 14px;
    overflow-y: scroll;
  }

  .newsDetail__head {
    position: relative;
    width: 100%;
    height: 30px;
    line-height: 1;
    color: #999999;
    padding: 3px 0 3px 20px;
  }
  .newsDetail__body {
    position: relative;
    width: 100%;
    padding-bottom: 140px;
    height: 1100px;
  }
  .newsDetail__body-explain,
  .newsDetail__body-header,
  .newsDetail__body-editor {
    position: relative;
    margin: 10px 20px;
  }
  .newsDetail__body-explain {
    color: #409eff;
    font-weight: bold;
    font-size: 16px;
  }
  .newsDetail__body-header {
    width: 56%;
    text-align: left;
  }
  .carousel__url,
  .imageNews__carousel {
    float: left;
    margin-right: 20px;
  }
  .carousel__url-input {
    width: 250px;
  }
  .newsDetail__body-editor {
    width: 100%;
    height: auto;
  }
  .newsDetail__body-editor::after {
    display: block;
    content: '';
    clear: both;
  }
  #editorElem {
    height: 520px;
    min-height: 400px;
  }
  .editor_left,
  .editor_right {
    float: left;
  }
  .editor_left {
    width: 84%;
    margin-right: 10px;
  }
  .editor_right-same1 > div,
  .editor_right-same2 > div,
  .editor_right-add > div,
  .editor_right-editor > div {
    margin-bottom: 10px;
  }
</style>
<style>
  .el-form-item__label {
    text-align: center;
  }
  .el-input-group__append {
    background-color: #ffffff;
    color: #f5131c;
    border: none;
  }
  .el-input__inner > *{
    display: inline-block;
  }
  .el-breadcrumb {
    display: inline-block;
    line-height: 1;
  }
  .el-breadcrumb__inner,
  .el-breadcrumb__inner a {
    font-weight: normal;
    color: #999999;
  }
  .el-breadcrumb__item:last-child .el-breadcrumb__inner,
  .el-breadcrumb__item:last-child .el-breadcrumb__inner:hover {
    color: #999999;
  }
</style>



