<template>
  <div class="addLab">
    <div class="addLab__body">
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="">
          <el-input v-model="form.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input-number v-model="form.floor" :min="1" :max="10" label="">
          </el-input-number>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.usages" placeholder="">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.vacant" placeholder="">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.building" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.description" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
          <el-button></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { apis } from '../../../config/url.js'
import $http from '../../../module/axios.js'
import Cookie from '../../../module/cookie.js'

export default {
  name: 'addLab',
  data () {
    return {
      form: {
        name: '',
        floor: 0,
        usages: '',
        vacant: '',
        building: '',
        description: ''
      }
    }
  },

  methods: {
    onSubmit() {
      var forms = this.form
      if (forms.name != "" || forms.name != null) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
           $http.post(apis.addLaboratory, forms)
          .then(res => {
            if (res.code == 1) {
              this.$message({
                type: 'success',
                center: true,
                message: '!'
              });

              // 
              this.form = {
                name: '',
                floor: 0,
                usages: '',
                vacant: '',
                building: '',
                description: ''
              }
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            center: true,
            message: ''
          });
        });
      }
    }
  }
}

</script>

<style scoped>
  .addLab {
    position: relative;
    width: inherit;
    height: inherit;
  }

  .addLab__body {
    position: absolute;
    left: 60%;
    top: 50%;
    width: 350px;
    transform: translateX(-50%) translateY(-50%);
  }
</style>



