<template>
  <div class="news" :style="newsNewStyle">
    <div class="news__head">
      <a href="javascript:;"  @click="refleshList"></a>
    </div>
    <div class="news__body">
      <div class="news__body-options">
        <el-button type="primary" size="mini" plain icon="el-icon-delete" @click="deleteMulti">
          
        </el-button>
        <router-link to="/news/add">
          <el-button type="primary" size="mini" icon="el-icon-circle-plus-outline" plain @click="addNews()">
            
          </el-button>
        </router-link>
        &nbsp;&nbsp;
        <div class="news__body-options-search">
          <div @keyup.enter="searchNews">
            <!--<el-button type="primary" size="mini" icon="el-icon-search" plain @click="searchNews"></el-button>-->
            <el-input placeholder="" v-model="searchInfo" class="input-with-select"
            suffix-icon="el-icon-edit" size="mini" >
              <el-select v-model="searchType" slot="prepend" placeholder="">
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
              </el-select>
              <el-button slot="append" icon="el-icon-search" @click="searchNews"></el-button>
            </el-input>

          </div>
        </div>
      </div>
      <div class="news__body-content">
        <div class="content__table">
          <el-table ref="multipleTable" :data="getNewsListPaging" tooltip-effect="dark"
          style="width: 100%" @selection-change="handleSelectionChange" stripe
          border :default-sort = "{prop: 'id', order: 'ascending'}" >

            <el-table-column type="selection" prop="id" width="55">
            </el-table-column>
            <el-table-column label="" width="160" prop="date" sortable >
              <template slot-scope="scope">
              {{ new Date(scope.row.date).format('yyyy-MM-dd hh:mm:ss', false) }}
              </template>
            </el-table-column>
            <el-table-column prop="author" label="" width="120">
            </el-table-column>
            <el-table-column prop="title" label="" show-overflow-tooltip>
            </el-table-column>
            <el-table-column prop="subtitle" label="" show-overflow-tooltip>
            </el-table-column>
            <el-table-column prop="state" label="" width="100">
              <template slot-scope="scope">
                {{ scope.row.state == 1? "":""}}
              </template>
            </el-table-column>
            <el-table-column  label="" width="100">
              <template slot-scope="scope">
                <router-link :to="{path:'/news/edit',query: {id: scope.row.id}}">
                  <i class="el-icon-edit" @click="editNews(scope.row.id)"></i>
                </router-link>
                &nbsp;&nbsp;
                <i class="el-icon-delete" @click="deleteSinple(scope.row.id)"></i>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="content__pagination">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[6, 8]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="newsNumber">
          </el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { apis } from '../../config/url.js'
import $http from '../../module/axios.js'
import { mapGetters, mapMutations } from 'vuex'

export default {
  name: 'news',
  data () {
    return {
      newsNewStyle: {},       // news
      currentPage: 1,         // 
      pageSize: 8,            // 8
      multipleSelection: [],  // checkbox
      searchInfo: '',         // 
      searchType: '',         // 
      selectIndexs: [],       // 
      editIndex: -1,          // 
      newsList: [],           // 
      newsNumber: 0           // 
    }
  },
  created() {
    let sh = document.documentElement.clientHeight
    this.newsNewStyle = {
      height: sh + 'px'
    }
    this.changePageStyle(this.getIsCollapse) // 

    // 
    this.updateList()
    this.countNewsNumber()
  },
  computed: {
    ...mapGetters ([
      'getIsCollapse',         // 
      'getOperationNewsIndex', // 1-/2-
      'getNewsListPaging'
    ])
  },
  methods: {
    /**
     * vuexmutation
     */
    ...mapMutations([
      'setIsCollapse',          // 
      'setOperationNewsIndex',  // 1-/2-
      'setNewsList'
    ]),
    /**
     * 
     */
    changePageStyle (val) {
      let sh = document.documentElement.scrollHeight
      if (val == true) {
        this.newsNewStyle = {
          left: '64px',
          width: 'calc(100% - 64px)',
          height: sh  + 'px',
          transition: 'width 0.35s, left 0.35s',
          MozTransition: 'width 0.35s, left 0.35s',
          WebkitTransition: 'width 0.35s, left 0.35s',
          OTransition: 'width 0.35s, left 0.35s'
        }
      } else {
        this.newsNewStyle = {
          left: '200px',
          width: 'calc(100% - 200px)',
          height: sh + 'px',
          transition: 'width 0.6s, left 0.6s',
          MozTransition: 'width 0.6s, left 0.6s',
          WebkitTransition: 'width 0.6s, left 0.6s',
          OTransition: 'width 0.6s, left 0.6s'
        }
      }
    },

    /**
     * 
     */
    updateList (current = this.currentPage, size = this.pageSize) {
      var _that = this
      /**
      * 
      */
      $http.post(apis.getNewsByPaging, {
        currentPage: current,  // 
        pageSize: size         // 
      })
      .then(res => {
        var datas = res.data.list
        // mutationstore
        _that.setNewsList(datas)
      })
    },
    /**
     * 
     */
    countNewsNumber () {
      var _that = this
      /**
      * 
      */
      $http.post(apis.getNewsNumber,{})
      .then(res => {
        // 
        _that.newsNumber = res.number
      })
    },

    /**
     * 
     */
    refleshList () {
      this.updateList()
      this.countNewsNumber()
    },
    /**
     * :
     */
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    /**
     * :
     */
    handleSelectionChange(selection) {
      this.multipleSelection = selection;
    },
    /**
     * :
     */
    deleteSinple(id) {
      if (id != null) {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          // 
          $http.post(apis.deleteNewsById, {
            id: id,
          })
          .then(res => {
            if (res.code== 1) {
              this.$message({
                type: 'success',
                message: '!',
                center: true
              })

              // 
              this.updateList()
              this.countNewsNumber()
            } else {
              this.$message({
                type: 'error',
                message: '!',
                center: true
              })
            }
          })
        })
        .catch((err) => {
        this.$message({
          type: 'info',
          message: '',
          center: true
        })
      })
      } else {
        this.$message({
          type: 'error',
          message: '!',
          center: true
        })
      }
    },
    /**
     * :
     */
    deleteMulti() {
      // 
      var arr = this.multipleSelection

      if (arr.length === 0) {
        this.$message({
          type: 'warning',
          message: '!',
          center: true
        })
        return;
      } else {
        var list = []
        // ID
        for (var i =0; i < arr.length; i++) {
          list.push(arr[i].id)
        }

        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          // 
          $http.post(apis.deleteMultiNews, {
            idList: list,
          })
          .then(res => {
            if (res.code== 1) {
              this.$message({
                type: 'success',
                message: '!',
                center: true
              })

              // 
              this.updateList()
              this.countNewsNumber()
            } else {
              this.$message({
                type: 'error',
                message: '!',
                center: true
              })
            }
          })
        })
        .catch((err) => {
          this.$message({
            type: 'info',
            message: '',
            center: true
          })
        })
      }
    },
    /**
     * :
     */
    searchNews() {
      var value = this.searchInfo // 
      var type = this.searchType  // 1-/2-

      if (value == null || value == '') {
        this.$message({
          type: 'warning',
          message: '',
          center: true
        })
        return;
      } else {
        var _that = this
        if (type == 1) {        // 
          // 
          $http.post(apis.getNewsByTitle, {
            title: value
          })
          .then(res => {
            if (res.code== 1) {
              var datas = res.data.list
              // mutationstore
              _that.setNewsList(datas)
              // 
              _that.newsNumber = datas.length
            }
          })

        } else if (type == 2) {  // 
          $http.post(apis.getNewsByAuthor, {
            author: value
          })
          .then(res => {
            if (res.code== 1) {
              var datas = res.data.list
              // mutationstore
              _that.setNewsList(datas)
              // 
              _that.newsNumber = datas.length
            }
          })

        } else {
          this.$message({
            type: 'warning',
            message: '',
            center: true
          })
          return;
        }
      }
    },
    /**
     * :
     */
    addNews() {
      this.setOperationNewsIndex({
        operationType: 1,        // 1-/2-
        operationNewsIndex: -1   // ID-1
      })
    },
    /**
     * :
     */
    editNews(id) {
      this.setOperationNewsIndex({
        operationType: 2,    // 1-/2-
        operationNewsIndex: id   // ID,
      })
    },
    /**
     * :
     */
    handleSizeChange(size) {
      if (size != null && size > 0) {
        this.pageSize = size
        // 
        this.updateList(1, size)
      }
    },
    /**
     * :
     */
    handleCurrentChange(currentPage) {
      if (currentPage != null && currentPage > 0) {
        // 
        this.updateList(currentPage)
      }
    }
  },

  watch: {
    getIsCollapse: {
      handler (val, oldVal) {
        this.changePageStyle(val)
      }
    }
  }
}
</script>

<style scoped>
  .news {
    position: relative;
    top: 62px;
    left: 200px;
    width: calc(100% - 200px);
    min-width: 560px;
    overflow: hidden;
    overflow-y: scroll;
    font-size: 14px;
  }
  .news__head {
    position: relative;
    width: 100%;
    height: 30px;
    color: #999999;
    padding: 3px 0 3px 10px;
  }
  .news__head a {
    color: #409eff;
    text-decoration: none;
  }
  .news__body {
    position: relative;
    width: 100%;
    height: auto;
  }
  .news__body-options {
    position: relative;
    margin: 8px 10px;
  }
  /*--------*/
  .news__body-options-search {
    position: absolute;
    right: 0;
    display: inline-block;
    margin-bottom: 15px;
  }
  .news__body-options-search > div {
    margin: 0;
    float: left;
  }
  .news__body-options-search .el-button--mini {
    border-radius: 0 3px 3px 0;
  }
  .news__body-options-search::after {
    display: block;
    content: '';
    clear: both;
  }
  .search__input {
    width: 250px;
    border-radius: 3px 0 0 3px;
  }
  .search__input > input {
    border-radius: 3px 0 0 3px;
  }
  /*--------*/

  .news__body-content {
    width: 100%;
  }

  /*--------*/
  .content__table {
    margin: 5px 10px;
    border: 1px solid #ebeef5;
  }
  .el-icon-edit,
  .el-icon-delete {
    color: #409eff;
    font-size: 18px;
    cursor: pointer;
  }
  .el-icon-edit:hover,
  .el-icon-delete:hover {
    color: orange;
  }
  /*--------*/

  /**/
  .content__pagination {
    margin-top: 20px;
    text-align: center;
  }

</style>
<style>
  .el-select .el-input {
    width: 100px;
  }
  .input-with-select .el-input-group__prepend {
    background-color: #ffffff;
  }
  .el-input-group__append,
  .el-input-group__prepend {
    padding: 0 15px;
  }
  .el-input-group__append button.el-button {
    color: #409eff;
  }
  .el-input-group__append button.el-button:hover {
    color: 	#ffa500;
  }
  .news__body-options-search .el-select
  .el-input.is-focus .el-input__inner {
    width: 160px;
  }
  .el-input-group>.el-input__inner {
    width: 210px;
  }
</style>



