<template>
  <div class="labList" :style="laboratoryNewStyle">
    <div class="labList__head">
       > 
    </div>
    <div class="labList__body">
      <div class="labList__body-left">
        <div class="labList__body-operation">
          <i class="el-icon-refresh" @click="fleshList"></i>
          &nbsp;&nbsp;
          <router-link :to="{ path: '/laboratory/labList/addLab', query:{ index: 0 } }"><i class="el-icon-circle-plus-outline"></i></router-link>
        </div>
        <!--  -->
        <ul class="list-ul">
          <li class="list-ul-li" v-for="item in labList" :key="item.id">
            <div class="li-div1">
              <router-link :to="{ path: '/laboratory/labList/labDetail', query:{ index: item.id} }" @click="labdetail(item.id)">{{ item.name }}</router-link>
            </div>
            <div class="li-div2">
              <img src="../assets/free.png" v-if="item.vacant == 1">
              <img src="../assets/busy.png" v-else>
            </div>
            <div class="li-div3">
            <router-link :to="{ path: '/laboratory/labList/editorLab', query:{ index: item.id } }">
            <i class="el-icon-edit-outline"></i>
            </router-link>
            </div>
            <div class="li-div4"><i class="el-icon-delete" @click="deleteLab(item.id)"></i></div>
          </li>
        </ul>

        <!--  -->
        <!--
        <div class="slb-pagination" v-show="isShow">
          <div class="slb-pagination-block">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="currentPage"
              :page-sizes="paseSizeAll"
              :page-size="pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="listNumber">
            </el-pagination>
          </div>
        </div>
        -->

      </div>
      <div class="labList__body-right" v-if="$route.query.index == undefined">
        <nothing></nothing>
      </div>
      <div class="labList__body-right" v-else>
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
import { apis } from '../../../config/url.js'
import $http from '../../../module/axios.js'
import Nothing from './nothing.vue'
import Cookie from '../../../module/cookie.js'
import { mapGetters, mapMutations } from 'vuex'

export default {
  name: 'laboratory',
  components: {
    Nothing
  },
  data () {
    return {
      laboratoryNewStyle: {}, // 
      activeNames: ['1'],
      labList: [],        // 
      isShow: true,
      floor: '',
      usage: '',
      vacant: '',
      currentPage: 1,     // 
      pageSize: 10,       // 10
      listNumber: 40,     // 
      paseSizeAll: [8, 10, 12, 14],
      floors: [       // 
        { value: 'all', label: '' },
        { value: '1', label: '1' },
        { value: '2', label: '2' },
        { value: '3', label: '3' }
      ],
      usages: [        // 
        { value: 'all', label: '' },
        { value: '1', label: '' },
        { value: '2', label: '' },
        { value: '3', label: '' }
      ],
      vacants: [       // 
        { value: 'all', label: '' },
        { value: '1', label: '' },
        { value: '2', label: '' },
      ],

    }
  },
  created () {
    let sh = document.documentElement.scrollHeight
    this.laboratoryNewStyle = {
      height: sh + 'px'
    }
    this.changePageStyle(this.getIsCollapse) // 

    // ,
    if (!Cookie.get('CURRENTUSER') || Cookie.get('USERTYPE') != '1') {
      this.$router.push('/home')
      return false
    }

    // 
    this.getList()
  },
  computed: {
    ...mapGetters ([
      'getIsCollapse'
    ])
  },
  methods: {
    /**
     * vuexmutation
     */
    ...mapMutations([
      'setIsCollapse'
    ]),

    /**
     * 
     */
    getList (current = this.currentPage, size = this.pageSize) {
      var _that = this
      $http.post(apis.getLaboratorysByPaging, {
        currentPage: current,
        pageSize: size
      })
      .then(res => {
        _that.labList = res.data.list
        _that.isShow = true
      })
    },
    /**
    * 
    */
    getTotal () {
      var _that = this
      $http.get(apis.getLaboratorysNumber)
      .then(res => {
        // 
        _that.listNumber = res.number
        _that.isShow = true
      })
    },

    /**
     * :
     */
    handleSizeChange(size) {
      if (size != null && size > 0) {
        this.pageSize = size
        // 
        this.getList(1, size)
      }
    },
    /**
     * :
     */
    handleCurrentChange(currentPage) {
      if (currentPage != null && currentPage > 0) {
        this.currentPage = currentPage
        // 
        this.getList(currentPage)
      }
    },

    /**
     * 
     */
    fleshList () {
      this.getList()
    },

    /**
     * 
     */
    deleteLab (id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        $http.post(apis.removeLaboratoryById, {
          id: id
        })
        .then(res => {
          if (res.code == 1) {
            // 
            this.getList()
          }
        })

        this.$message({
          type: 'success',
          center: true,
          message: '!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          center: true,
          message: ''
        });
      });
    },

    /**
     * 
     */
    changePageStyle (val) {
      let sh = document.documentElement.scrollHeight
      if (val == true) {
        this.laboratoryNewStyle = {
          left: '64px',
          width: 'calc(100% - 64px)',
          height: sh  + 'px',
          transition: 'width 0.35s, left 0.35s',
          MozTransition: 'width 0.35s, left 0.35s',
          WebkitTransition: 'width 0.35s, left 0.35s',
          OTransition: 'width 0.35s, left 0.35s'
        }
      } else {
        this.laboratoryNewStyle = {
          left: '200px',
          width: 'calc(100% - 200px)',
          height: sh + 'px',
          transition: 'width 0.6s, left 0.6s',
          MozTransition: 'width 0.6s, left 0.6s',
          WebkitTransition: 'width 0.6s, left 0.6s',
          OTransition: 'width 0.6s, left 0.6s'
        }
      }
    }
  },

  watch: {
    getIsCollapse: {
      handler (val, oldVal) {
        this.changePageStyle(val)
      }
    }
  }
}
</script>

<style scoped>
  .labList {
    position: relative;
    top: 62px;
    left: 200px;
    width: calc(100% - 200px);
    min-width: 560px;
    overflow: hidden;
    overflow-y: scroll;
    font-size: 14px;
  }
  .labList a {
    text-decoration: none;
    color: #409eff;
  }
  .labList a:hover {
    text-decoration: none;
    color: orange;
  }
  .labList__head {
    position: relative;
    width: 100%;
    height: 30px;
    color: #999999;
    padding: 3px 0 3px 10px;
  }
  .labList__body {
    position: relative;
    width: 100%;
    height: auto;
    padding: 5px 0 5px 10px;
  }
  .labList__body::after {
    display: block;
    content: '';
    clear: both;
  }
  .labList__body-left,
  .labList__body-right {
    float: left;
  }
  .labList__body-left {
    width: 25%;
    height: auto;
  }
   .labList__body-operation {
    margin-bottom: 20px;
  }

  .labList__body-right {
    width: 74%;
    height: 550px;
    border-left: 1px solid #D8D8D8;
    border-top: 1px solid #D8D8D8;
  }
  /**/
  .basicList {
    padding-left: 0;
  }
  /**/
  .basicList__item {
    position: relative;
    display: flex;
    width: 100%;
    height: 48px;
    line-height: 48px;
    padding-left: 10px;
    cursor: pointer;
  }
  /**/
  .basicList__item:hover {
    background-color: #ecebec;
  }
  /**/
  .basicList__item.active {
    background-color: #ecebec;
  }

  /*
   *  
  */
  .list-ul {
    padding-left: 10px;
    list-style: none;
  }
  .list-ul-li {
    padding-top: 6px;
    padding-bottom: 5px;
    border-bottom: 1px dotted #dcdcdc;
    list-style-type: none;
  }
  .list-ul-li > div {
    display: inline-block;
  }
  .list-ul-li > div:first-child {
    width: 40%;
  }
  .list-ul-li > div:nth-child(2) {
    width: 30%;
  }
  .list-ul-li > div:nth-child(3) {
    width: 10%;
  }
  .list-ul-li > div:nth-child(4) {
    width: 10%;
  }


  /**/
  .el-icon-refresh,
  .el-icon-circle-plus-outline,
  .el-icon-delete,
  .el-icon-edit-outline {
    font-size: 16px;
    color: #409eff;
    cursor: pointer;
  }

  .el-icon-refresh:hover,
  .el-icon-circle-plus-outline:hover,
  .el-icon-delete:hover,
  .el-icon-edit-outline:hover {
    color: #FF8C00;
  }



</style>
<style>
  /**/
  ::-webkit-scrollbar {
    color: #ecebec;
    background-color: #ecebec 100%;
  }
  /**/
  .el-collapse-item__header {
    height: 48px !important;
    font-size: 15px !important;
    color: #3a3a53 !important;
    font-weight: 500 !important;
    border-bottom: 1px solid #ecebec;
  }
  /**/
  .el-collapse-item__wrap {
    background-color: #ffffff !important;
    border-bottom: 1px solid #ecebec;
  }
  /**/
  .el-collapse-item__content {
    padding: 0 !important;
  }
</style>



