<template>
  <div class="editorLab">
    <div class="editorLab__body">
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="">
          <el-input v-model="form.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input-number v-model="form.floor" :min="1" :max="10" label="">
          </el-input-number>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.usages" placeholder="">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.vacant" placeholder="">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.building" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.description" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
          <el-button></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { apis } from '../../../config/url.js'
import $http from '../../../module/axios.js'
import Cookie from '../../../module/cookie.js'

export default {
  name: 'editorLab',
  data () {
    return {
      form: {
        id: '',
        name: '',
        floor: 0,
        usages: '',
        vacant: '',
        building: '',
        description: ''
      },
      usagesLabel: '',
      vacantLabel: ''
    }
  },

  created () {
    this.getInfo()
  },

  methods: {

    /**
     * 
     */
    onSubmit() {
      var forms = this.form
      if (forms.name != "" || forms.name != null) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          $http.post(apis.updateLaboratoryById, forms)
          .then(res => {
            if (res.code == 1) {
              this.$message({
                type: 'success',
                center: true,
                message: '!'
              });

              // 
              this.form = {
                name: '',
                floor: 0,
                usages: '',
                vacant: '',
                building: '',
                description: ''
              }
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            center: true,
            message: ''
          });
        });
      }
    },

    /**
     * 
     */
    getInfo () {
      var id = this.$route.query.index
      $http.post(apis.getLaboratoryById, {
        id: id
      })
      .then(res => {
        this.form = res.data.laboratory

        if (this.form.usages == 1) {
          this.form.usages = ''
        } else {
           this.form.usages = ''
        }

        if (this.form.vacant == 1) {
          this.form.vacant = ''
        } else {
           this.form.vacant = ''
        }

      })
    }
  },

  //
  watch: {
    // 
    "$route": function(newValue,oldValue){
      this.getInfo()
    }
  }
}

</script>

<style scoped>
  .editorLab {
    position: relative;
    width: inherit;
    height: inherit;
  }

  .editorLab__body {
    position: absolute;
    left: 60%;
    top: 50%;
    width: 350px;
    transform: translateX(-50%) translateY(-50%);
  }
</style>



