/**
 * 
 * import FormaterTime from './time.js'
 * FormaterTime();
 * new Date().format('yyyy-MM-dd hh:mm:ss', false)
 * 
 */


function formaterTime () {
  Date.prototype.format = function (fmt = 'yyyy-MM-dd HH:mm:ss', isweek) {
    var o = {
      'M+': this.getMonth() + 1,                   // 
      'd+': this.getDate(),                        // 
      'h+': this.getHours(),                       // 
      'm+': this.getMinutes(),                     // 
      's+': this.getSeconds(),                     // 
      'q+': Math.floor((this.getMonth() + 3) / 3), // 
      'S': this.getMilliseconds()                  // 
    }
    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (this.getFullYear() + '').substr(4 - RegExp.$1.length))
    }
    for (var k in o) {
      if (new RegExp(`(${k})`).test(fmt)) {
        fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
      }
    }

    // 
    if(isweek === true) {
      // 
      var week = ['','','','','','','']
      var w = week[this.getDay()];   // 
      fmt += '  ' + w;
    }
    return fmt
  }
}

export default formaterTime



